.form {
  display: flex;
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
  height: 3.4rem;
  width: 40%;
  position: absolute;
  right: 2rem;
  top: 1.5rem;
}

.expandedForm {
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
  height: 5.4rem;
  width: 90%;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: 1.5rem;
  text-align: center;
  display: flex;
  z-index: 2;
}

.expandedButton {
  border: none;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  padding: 0;
  width: 127px;
  height: 48px;
  background: #eb5757;
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
  color: #fff;
}

.buttonWrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.input {
  border-right: 1px solid #f2f2f2;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

