.button {
  background: none;
  color: inherit;
  border: none;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  padding: 2px;
  vertical-align: middle;
  margin: 1rem 1rem 1rem 1rem;
  display: inline-flex;
  align-items: center;
}

.text {
  padding-left: 0.5rem;
}

.icon {
  vertical-align: middle;
}

.backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.356);
}

.hidden {
  display: none;
}

.expandedWrapper {
  position: absolute;
  top: 0;
  left: 0rem;
  width: 100%;
  background-color: #fff;
  z-index: 1;
  display: flex;
}

.expandedItems {
  margin-top: 8rem;
  width: 100%;
  text-align: center;
}

/* hidden in mobile view */
.expandedItemsHidden {
  margin-top: 8rem;
  width: 100%;
  text-align: center;
}

.number {
  display: inline-block;
  margin: 1rem;
}

.list {
  padding: 0;
  margin: 0 0 0 10%;
  text-align: center;
}

.listItem {
  list-style-type: none;
  margin: 1rem 0 0 0.5rem;
  cursor: pointer;
  display: inline-block;
  vertical-align: super;
}

@media screen and (max-width: 550px) {
  .form {
    position:static;
    width:90%;
    margin:auto;
  }

  .buttonWrapper {
    flex-basis: content;
  }

  .expandedItemsHidden {
    display: none;
  }

  .expandedWrapper {
    flex-direction: column;
  }

  .expandedButton {
    position: absolute;
    top: 20rem;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    text-align: center;
  }
}